html {
  background: lightskyblue;
}

.App {
  text-align: center;
}

button {
  border-radius: 10px;
  padding: 0.5rem;
  background: plum;
  font-weight: 700;
}

.main-container {
  margin-top: 20%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
}

@media only screen and (max-width: 700px) {
  button {
    font-size: 8px;
    padding: 0.4rem;
  }
  p {
    font-size: 12px;;
  }
  .main-container {
    margin-top: 45%;
  }
}
 